import React from "react";
import HelloReact from "./components/HelloReact";
import GoodbyeReact from "./components/GoodbyeReact";
import HelloDiv from "./components/HelloDiv";
import Calculator from "./components/Calculator";
import List from "./components/List";
import Counter from "./components/Counter";
import Form from "./components/Form";
import SearchResultContainer from "./components/SearchResultContainer/SearchResultContainer";
import OmdbContainer from "./components/OmdbContainer/OmdbContainer";
import { BrowserRouter as Router, Route } from "react-router-dom";
import NavTabs from "./components/NavTabs";
import Home from "./components/pages/Home";
import About from "./components/pages/About";
import Blog from "./components/pages/Blog";
import Contact from "./components/pages/Contact";
// import FriendsCards from "./components/pages/FriendsCards";

const groceries = [
  {
    id: 1,
    name: "Milk",
    purchased: true
  },
  {
    id: 2,
    name: "Eggs",
    purchased: true
  },
  {
    id: 3,
    name: "Cheese",
    purchased: false
  },
  {
    id: 4,
    name: "Cake Mix",
    purchased: false
  },
  {
    id: 5,
    name: "Carrots",
    purchased: false
  },
  {
    id: 6,
    name: "Juice",
    purchased: true
  }
];

const App = () => {
  return( 
    <div>

      <Router>
        <div>
          <NavTabs />
          <Route exact path="/" component={Home} />
          <Route exact path="/about" component={About} />
          <Route exact path="/blog" component={Blog} />
          <Route exact path="/contact" component={Contact} />
          <Route exact path="/hello" component={HelloReact} />
          <Route exact path="/goodbye" component={GoodbyeReact} />
          <Route exact path="/goodbye" component={HelloDiv} />
          <Route exact path="/goodbye" component={Calculator} />
          {/* <Route path="/friends" component={FriendsCards} /> */}
          <Route exact path="/groceries" component={groceries} />
          <Route exact path="/form" component={Form} />
          <Route exact path="/searchResults" component={SearchResultContainer} />
          <Route exact path="/omdb" component={OmdbContainer} />
          <Route exact path="/counter" component={Counter} />
        </div>
      </Router>
      
    </div>
  );
}

export default App;



